import React from 'react'
import classes from './Footer.module.css'
import Logo from '../../logo.png'
import {NavLink, Link} from 'react-router-dom'
const  Footer = () => {
return(
    <div className={classes.Footer}>
        <div className="container">
            <div className={classes.footerInner}>
            <div className={classes.FooterLogoSection}>
            <img src={Logo} alt="Azato.ru"/>
            <span>  </span>
            </div>
            <div className={classes.FooterNavigationSection}>
            <nav>
                <ul>
                    <li><NavLink to="/"></NavLink> </li>
                    <li><NavLink to="/details"></NavLink></li>
                    <li><NavLink to="/requests"></NavLink></li>
                </ul>
                
                
            </nav>
            </div>
            <div className={classes.FooterAuthSection}>
                <div><Link to='/logout' className={classes.exitLink}> <i className="fa fa-sign-out" aria-hidden="true"></i></Link></div>
            </div>

            </div>
        </div>
        </div>
)

}

export default Footer
