import React, {useState} from 'react'
import classes from './Readme.module.css'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import Type1 from '../../../../img/readme/type1-1.PNG'
import Type12 from '../../../../img/readme/type1-2.PNG'
import Type13 from '../../../../img/readme/type1-3.PNG'
import Type2 from '../../../../img/readme/type2-1.PNG'
import Type21 from '../../../../img/readme/type2-2.PNG'
import Type22 from '../../../../img/readme/type2-3.PNG'
import Load1 from '../../../../img/readme/load1.PNG'
import Load2 from '../../../../img/readme/load2.PNG'
import Load3 from '../../../../img/readme/load3.PNG'
import Load4 from '../../../../img/readme/load4.PNG'
import Load5 from '../../../../img/readme/load5.PNG'
import Load6 from '../../../../img/readme/load6.PNG'
import Conv1 from '../../../../img/readme/conv1/conv1.PNG'
import Conv2 from '../../../../img/readme/conv1/conv2.PNG'
import Conv3 from '../../../../img/readme/conv1/conv3.PNG'
import Conv4 from '../../../../img/readme/conv1/conv4.PNG'
import Conv5 from '../../../../img/readme/conv1/conv5.PNG'
import Conv6 from '../../../../img/readme/conv1/conv6.PNG'
import Conv7 from '../../../../img/readme/conv1/conv7.PNG'
import Conv8 from '../../../../img/readme/conv1/conv8.PNG'
import Conv9 from '../../../../img/readme/conv1/conv9.PNG'
import Conv10 from '../../../../img/readme/conv1/conv10.PNG'
import Conv11 from '../../../../img/readme/conv1/conv11.PNG'
import Conv12 from '../../../../img/readme/conv1/conv12.PNG'
import Conv13 from '../../../../img/readme/conv1/conv13.PNG'
import Conv14 from '../../../../img/readme/conv2/conv14.PNG'
import Conv15 from '../../../../img/readme/conv2/conv15.PNG'
import Conv16 from '../../../../img/readme/conv2/conv16.PNG'
import Conv17 from '../../../../img/readme/conv2/conv17.PNG'
import Conv18 from '../../../../img/readme/conv2/conv18.PNG'
import Conv19 from '../../../../img/readme/conv2/conv19.PNG'
import Conv20 from '../../../../img/readme/conv2/conv20.PNG'
export const Readme = (props) =>{

    const images = [
        Type1,
        Type12,
        Type13,
        Type2,
        Type21,
        Type22,
        Conv1,
        Conv2,
        Conv3,
        Conv4,
        Conv5,
        Conv6,
        Conv7,
        Conv8,
        Conv9,
        Conv10,
        Conv11,
        Conv12,
        Conv13,
        Conv14,
        Conv15,
        Conv16,
        Conv17,
        Conv18,
        Conv19,
        Conv20,
        Load1,
        Load2,
        Load3,
        Load4,
        Load5,
        Load6
     ];

    const [imageIndex, setImageIndex] = useState(0);
    const [isOpen, setIsOpen] = useState(false);
    
    return (
        <div className={classes.Readme}>
            <h1>     -   AZATO.RU</h1>
            <ol>
                <li>  -   ,    ,           .</li>
                <li>    Excel.       ,       Excel-online     .</li>
                <li>        -:</li>
                </ol>
                <p> 1-    ()     :</p>
            
            <img src={Type1}  alt='' onClick={() => {setImageIndex(0); setIsOpen(true)} }/>
            <img src={Type12} alt=''  onClick={() => {setImageIndex(1); setIsOpen(true)}}/>
            <img src={Type13} alt=''  onClick={() => {setImageIndex(2); setIsOpen(true)}}/>
                <p> 2-    ()     :</p>
            <img src={Type2}  alt='' onClick={() => {setImageIndex(3); setIsOpen(true)}}/>
            <img src={Type21} alt=''  onClick={() => {setImageIndex(4); setIsOpen(true)}}/>
            <img src={Type22}  alt='' onClick={() => {setImageIndex(5); setIsOpen(true)}}/>
            <p>4.  -</p>
<p><strong>   :</strong></p>

<p>a.   1.</p>
<p>b.   -       1  </p>
<p>c.        .           ,   (  )      ,   ctrl + c</p>
<img src={Conv1} alt='' onClick={() => {setImageIndex(6); setIsOpen(true)}}/>
<p>d.       1.       1  Excel,   (  )   2 (     ,  2   1)  ,   ctrl + v    .</p>
<img src={Conv2} alt='' onClick={() => {setImageIndex(7); setIsOpen(true)}}/>
<img src={Conv3} alt='' onClick={() => {setImageIndex(8); setIsOpen(true)}}/>
<p>e.        .           ,   (  )      ,   ctrl + c.</p>
<img src={Conv4} alt='' onClick={() => {setImageIndex(9); setIsOpen(true)}}/>
<img src={Conv5} alt='' onClick={() => {setImageIndex(10); setIsOpen(true)}}/>
<p>f.     Excel      .             (  )   1  ,   ctrl + v    .</p>
<img src={Conv6} alt='' onClick={() => {setImageIndex(11); setIsOpen(true)}}/>
<img src={Conv7} alt=''  onClick={() => {setImageIndex(12); setIsOpen(true)}}/>
<img src={Conv8}  alt='' onClick={() => {setImageIndex(13); setIsOpen(true)}}/>
<p><i>**:    -   ,     ( ;  ;  ; )          .    -          . </i></p>
<img src={Conv9} alt=''  onClick={() => {setImageIndex(14); setIsOpen(true)}}/>
<p>g.     CSV.        , -       CSV  .</p>
<img src={Conv10}  alt='' onClick={() => {setImageIndex(15); setIsOpen(true)}}/>
<img src={Conv11}  alt='' onClick={() => {setImageIndex(16); setIsOpen(true)}}/>
<img src={Conv12} alt=''  onClick={() => {setImageIndex(17); setIsOpen(true)}}/>
<img src={Conv13} alt=''  onClick={() => {setImageIndex(18); setIsOpen(true)}}/>
<p><strong>   :</strong></p>
<p>a.   2.</p>
<p>b.   -       2  </p>
<p>c.     .           ,   (  )      ,   ctrl + c.</p>
<img src={Conv14} alt=''  onClick={() => {setImageIndex(19); setIsOpen(true)}}/>
<img src={Conv15} alt=''  onClick={() => {setImageIndex(20); setIsOpen(true)}}/>
<p>d.        2.       2  Excel,   (  )   1  ,   ctrl + v    .</p>
<img src={Conv16} alt=''  onClick={() => {setImageIndex(21); setIsOpen(true)}}/>
<p>e.       ,   2,    . (       )</p>
<img src={Conv9} alt=''  onClick={() => {setImageIndex(14); setIsOpen(true)}}/>
<img src={Conv17} alt=''  onClick={() => {setImageIndex(22); setIsOpen(true)}}/>
<img src={Conv18} alt=''  onClick={() => {setImageIndex(23); setIsOpen(true)}}/>
<p> <i>**:      -  (;   ..)       ,     ,      . </i></p>
<p>f.    .         ( ).           ,   (  )      ,   ctrl + c.</p>
<img src={Conv19} alt=''  onClick={() => {setImageIndex(24); setIsOpen(true)}}/>
<p>g.     CSV.        , -       CSV  .</p>
<img src={Conv6}  alt='' onClick={() => {setImageIndex(11); setIsOpen(true)}}/>
<img src={Conv7}  alt='' onClick={() => {setImageIndex(12); setIsOpen(true)}}/>
<img src={Conv20} alt=''  onClick={() => {setImageIndex(25); setIsOpen(true)}}/>
<p>h.     CSV.        , -       CSV  .</p>
<img src={Conv10} alt=''  onClick={() => {setImageIndex(15); setIsOpen(true)}}/>
<img src={Conv11}  alt='' onClick={() => {setImageIndex(16); setIsOpen(true)}}/>
<img src={Conv12} alt=''  onClick={() => {setImageIndex(17); setIsOpen(true)}}/>
<img src={Conv13}  alt='' onClick={() => {setImageIndex(18); setIsOpen(true)}}/>

<p>5.         AZATO.RU.      ,     .  ,   ,      .</p>
<img src={Load1} alt=''  onClick={() => {setImageIndex(26); setIsOpen(true)}}/>
<img src={Load2} alt=''  onClick={() => {setImageIndex(27); setIsOpen(true)}}/>
<img src={Load3} alt=''  onClick={() => {setImageIndex(28); setIsOpen(true)}}/>
<p>6.         .</p>
<img src={Load4} alt=''  onClick={() => {setImageIndex(29); setIsOpen(true)}}/>
<p>7.      .            </p>
<img src={Load5}  alt='' onClick={() => {setImageIndex(30); setIsOpen(true)}}/>
<img src={Load6}  alt='' onClick={() => {setImageIndex(31); setIsOpen(true)}}/>


{isOpen && (
      <Lightbox
        mainSrc={images[imageIndex]}
        nextSrc={images[(imageIndex + 1) % images.length]}
        prevSrc={images[(imageIndex + images.length - 1) % images.length]}
        onCloseRequest={() => setIsOpen(false)}
        onMovePrevRequest={() =>
           
            setImageIndex((imageIndex + images.length - 1) % images.length)
           
          }
          onMoveNextRequest={() =>
            setImageIndex((imageIndex + 1) % images.length)}
      />
    )}
        </div>
    )

}


